body {
  margin: 0;
  font-family: Jost, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
    'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.slick-slider {
  width: 95px;
  height: 643px;
}
.slick-list {
  width: 95px;
  height: 643px;
}
.slick-track {
  height: 643px;
}
.slick-arrow {
  width: 95px;
  height: 30px;
  margin-top: 0;
  margin-top: 17px;
  margin-bottom: 17px;
  border: none;
  background-color: #9fcb22;
}
.slick-arrow:hover {
  cursor: pointer;
}
.slick-next {
  margin-top: 17px;
  
}
